<template>
<el-dialog class="exchange-detail" title="詳情" :visible="showDialog" @close="onClose()" width="800">
    <div v-if="step == 1">
        <img class="img" :src="exchangeItem.img" alt="">
        <p>物品類型：{{exchangeItem.category}}</p>
        <p>專輯：{{exchangeItem.groupName}} {{exchangeItem.album}}</p>
        <p>{{exchangeItem.desc}}</p>
        <p><span v-for="(member) in exchangeItem.ownMember" :key="member">{{member}} </span> => <span v-for="(member,i) in exchangeItem.targetMember" :key="i">{{member}} </span></p>
        <p>可 <span v-for="(way) in exchangeItem.exchangeWay" :key="way">{{way}}</span></p>
        <p><span v-for="(note) in exchangeItem.note" :key="note">#{{note}} </span></p>
        <el-form>
          <el-form-item label="選擇擁有成員">
            <el-radio-group v-model="form.targetMember">
              <el-radio v-for="(member,i) in exchangeItem.targetMember" :label="member" :key="i" border>{{member}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="選擇欲交換成員">
            <el-radio-group v-model="form.ownMember" size="small">
              <el-radio v-for="(member,j) in exchangeItem.ownMember" :label="member" :key="j" border>{{member}}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="交換方式">
            <el-checkbox-group v-model="form.exchangeWay">
              <el-checkbox v-for="(way,i) in exchangeItem.exchangeWay" :label="way"  :key="i"  name="way"></el-checkbox>
          </el-checkbox-group>
          </el-form-item>
        </el-form>
        
        
       
        
        
    </div>
    <div v-if="step == 2">
      <p>{{form.targetMember}}換{{form.ownMember}}的{{exchangeItem.category}}</p>
      <p>填寫交換訊息</p>
      <el-input type="textarea" v-model="form.msg" placeholder="請填入訊息"></el-input>
    </div>
     <div slot="footer" class="dialog-footer">
        <el-button @click="onClose()">{{step == 1?"取消":"上一步"}}</el-button>
        <el-button type="primary" @click="onSubmit()">{{step == 1?"下一步":"確認送出"}}</el-button>
    </div>
</el-dialog>
</template>

<script>
import { applyExchangeItem  } from '../../../api/exchange'; 
export default {
  props: {
        showDialog: {
            type: Boolean,
            require: true
        },
        exchangeItem:{
          require:true
        }
    },
    data(){
      return {
        step:1,
        form:{
          targetMember:[],
          ownMember:[],
          exchangeWay:[],
          msg:""
        }
      }
    },
    computed:{
    },
    methods:{
      onClose(){
            if (this.step == 2){
                this.step = 1
                return
            }
            this.$emit('update:showDialog', false)
        },
        onSubmit(){
            this.submitItem()
        },
        submitItem(){
            let params = this.form
            params.exchangeItemId = this.exchangeItem.exchangeItemId
            if (this.step == 1){
                this.form.msg = `您好，我有${this.form.targetMember}想跟您換${this.form.ownMember}`
                this.step = 2
                return 
            } 
            //  applyExchangeItem(params).then(res => {
            //     if (res.status == 200) {
            //         console.log(res.data);
            //         }
            //     }).catch(err => {
            //         console.log(err);
            //     });
            this.$emit('update:showDialog', false)
        },
    }

}
</script>

