<template>
  <div class="exchange">
      <!-- <div class="add" @click="popAddExchange()">+ 新增</div> -->
      <searchExchange></searchExchange>
      <exchangeItem :itemList = "allItems"></exchangeItem>
      <addExchange :showDialog.sync="showAddExchange"></addExchange>
  </div>
</template>

<script>
import addExchange from './components/addExchange.vue'
import searchExchange from './components/searchExchange.vue'
import exchangeItem from './components/exchangeItem.vue'
import { getItems  } from '../../api/exchange'; 

export default {
    name: 'Exchange',
    components:{
        addExchange,
        searchExchange,
        exchangeItem,
    },
    data(){
        return {
            allItems: [],
            showAddExchange: false,
        }
    },
    mounted(){
        this.getItems()
    },
    methods:{
        popAddExchange(){
            this.showAddExchange = true
        },
        getItems(){
            let params = this.form
             getItems(params).then(res => {
                if (res.status == 200) {
                    console.log(res.data);
                    this.allItems = res.data
                    }
                }).catch(err => {
                    console.log(err);
                });
        }
    }
}
</script>

