import request from '../utils/https'

// 交換物品列表
export function getItems (params) {
    return request({
        url: '/exchange/getItem',
        method: 'post',
        data:params
    })
}

// 新增交換物品
export function addItem (params) {
    return request({
        url: '/exchange/addItem',
        method: 'post',
        data:params
    })
}

// 刪除交換物品
export function deleteItem (params) {
    return request({
        url: '/exchange/deleteItem',
        method: 'post',
        data:params
    })
}

// 提交交換申請
export function applyExchangeItem (params) {
    return request({
        url: '/exchange/applyExchangeItem',
        method: 'post',
        data:params
    })
}